:host {
  width: 100%;
  height: 100%;
}

#resetPassword-component {
  .screen-card {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    display: flex;
    flex-direction: column;
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.93);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    padding: 46px 52px 65px 60px;
    max-width: 420px;
    width: 420px;
    .logo {
      display: flex;
      justify-content: center;
      img {
        width: 176px;
        height: 40px;
      }
    }

    .input-heading {
      color: #000;
      font-size: 20px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      padding-top: 32px;
    }

    .form-group {
      padding-top: 12px;
      margin-bottom: 0;
      mat-form-field {
        width: 100%;
      }
    }

    .input-bottom {
      a {
        color: #0f72ad;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        cursor: pointer;
      }
    }

    .btns-section {
      padding-top: 32px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .anchor {
        a {
          color: #0f72ad;
          font-size: 14px;
          font-style: normal;
          font-weight: 400;
          line-height: normal;
          cursor: pointer;
        }
      }

      .login-btn {
        max-width: 160px;

        button {
          border-radius: 45px;
          background: #142430;
          width: 100%;
          border: none;
          padding: 12px 24px;
          font-size: 14px;
        }
      }
    }
  }

  .term-of-services {
    bottom: 0%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    color: var(--basic-white, #fff);
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;

    a {
      text-decoration-line: underline;
      cursor: pointer;
    }

    .version-text {
      padding-top: 24px;
    }
  }
}

.bg-image {
  background-image: url('../../../../assets/img/default-login.jpg');
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;

  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(39, 52, 59, 0.7);
  }
}

::ng-deep {
  .mat-form-field.mat-focused .mat-form-field-outline-thick {
    color: #2cace2 !important;
  }

  .mat-form-field.mat-focused .mat-form-field-label {
    color: #2cace2 !important;
  }

  .mat-mdc-form-field.mat-focused .mdc-notched-outline {
    border-color: #2cace2 !important;
  }

  /* Change the label color when focused */
  .mat-mdc-form-field.mat-focused .mdc-floating-label {
    color: #2cace2 !important;
  }

  .mdc-text-field--outlined:not(.mdc-text-field--disabled).mdc-text-field--focused .mdc-notched-outline__trailing {
    border-color: #2cace2 !important;
  }
  .mdc-text-field--outlined:not(.mdc-text-field--disabled).mdc-text-field--focused .mdc-notched-outline__notch {
    border-color: #2cace2 !important;
  }
  .mdc-text-field--outlined:not(.mdc-text-field--disabled).mdc-text-field--focused .mdc-notched-outline__leading {
    border-color: #2cace2 !important;
  }

  .mat-mdc-form-field-input-control.mat-mdc-form-field-input-control {
    font-size: 14px !important;
    color: #000 !important;
  }
}
